body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #667292;
  box-sizing: border-box;
}

/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
} */

header {
  background: #667292;
  color: #f1e3dd;
  font-family: "PT Sans", Arial, Helvetica, sans-serif;
  font-size: 20px;
  padding: 5px;
  text-align: center;
  letter-spacing: 5px;
}

div.content {
  margin: 50px;
  text-align: center;
}

.create-Btn {
  display: inline-block;
  padding: 15px 20px;
  font-size: 14px;
  font-family: "PT Sans", Helvetica, Arial, sans-serif;
  font-weight: bold;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #f1e3dd;
  background-color: #8d9db6;
  border: none;
  border-radius: 5px;
  /* box-shadow: 0 2px #999; */
  margin-top: 20px;
}

.create-Btn:hover {
  background-color: #667292;
  letter-spacing: 5px;
  transition: all .4s ease;
}

.create-Btn:active {
  background-color: #e0876a;
  /* box-shadow: 0 5px #666; */
  transform: translateY(4px);
}

.box-url-display {
  background: #f1e3dd;
  border-radius: 5px;
  width: 385px;
  height: 20px;
  text-align: center;
  margin: auto;
  padding: 10px;
}

.request-container {
  border: 1px solid #bccad6;
  margin-bottom: 10px;
  padding: 10px;
  text-align: left;
}